import {Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {ColumnMode, DatatableComponent} from '@swimlane/ngx-datatable';

import {icons} from '../../../../auth/helpers';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {UserEditComponent} from '../user-edit/user-edit.component';
import {UserService} from '../../../../auth/service';
import {User1} from '../../../../auth/models';
import {ToastrService} from 'ngx-toastr';
import {UserAddComponent} from '../user-add/user-add.component';
import {RolesService} from '../../roles/roles.service';
import {UserLimitsComponent} from "../user-limits/user-limits.component";
import Swal from "sweetalert2";

@Component({
    selector: 'app-user-list',
    templateUrl: './user-list.component.html',
    styleUrls: ['./user-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class UserListComponent implements OnInit {

    constructor(
        private modal: NgbModal,
        private toastr: ToastrService,
        private role: RolesService,
        private user: UserService,
    ) {
    }

    // Public
    public sidebarToggleRef = false;
    public users: User1[];
    public originalUsers: User1[];
    public selectedOption = 5;
    public ColumnMode = ColumnMode;
    public temp = [];
    public previousRoleFilter = '';
    public previousPlanFilter = '';
    public previousStatusFilter = '';

    public selectRole: any = [
        {name: 'All', value: ''},
        {name: 'Admin', value: 'Admin'},
        {name: 'Author', value: 'Author'},
        {name: 'Editor', value: 'Editor'},
        {name: 'Maintainer', value: 'Maintainer'},
        {name: 'Subscriber', value: 'Subscriber'}
    ];

    public selectPlan: any = [
        {name: 'All', value: ''},
        {name: 'Basic', value: 'Basic'},
        {name: 'Company', value: 'Company'},
        {name: 'Enterprise', value: 'Enterprise'},
        {name: 'Team', value: 'Team'}
    ];

    public selectStatus: any = [
        {name: 'All', value: ''},
        {name: 'Pending', value: 'Pending'},
        {name: 'Active', value: 'Active'},
        {name: 'Inactive', value: 'Inactive'}
    ];

    public selectedRole
    public selectedPlan = [];
    public selectedStatus = [];
    public searchValue = '';
    deletingUsers={}
    roles: { custom_role_id: number, custom_role_name: string }[];

    // Decorator
    @ViewChild(DatatableComponent) table: DatatableComponent;

    // Private
    private tempData = [];
    q: string;
    selectAll = false;
    icons = icons;
    loading = true;
    loadingRoles = true;

    ngOnInit(): void {
        this.loadUsers()
        this.role.getList().then(res => {
            this.roles = res.data;
            this.loadingRoles = false;
        });
    }
    loadUsers(){
        this.loading=true
        this.user.get().then(res => {
            this.originalUsers = this.users = res.data;
        }).catch(err => {
            this.toastr.error('Something went wrong.');
        }).finally(() => this.loading = false);
    }

    delete(user) {
        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#7367F0',
            cancelButtonColor: '#E42728',
            confirmButtonText: 'Yes, Remove this user',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            }
        }).then((result) => {
            if (result.value) {
                this.deletingUsers[user.id]=true
                this.user.delete(user.id).then(res => {
                    this.loadUsers()
                    Swal.fire({
                        icon: 'success',
                        title: 'Removed!',
                        text: 'This user Has Been Removed Successfully!',
                        customClass: {
                            confirmButton: 'btn btn-success'
                        }
                    })})
            }
        }).catch(error=>{
            Swal.fire({
                    icon: 'error',
                    title: 'Oops',
                    text: 'Something went wrong'
                }
            ).finally(()=>{
                delete(this.deletingUsers[user.id])
            })
        });
    }

    search(q: string) {
        q = q.toLowerCase();
        if (!q) {
            this.users = this.originalUsers;
            return;
        }
        this.users = this.originalUsers.filter(u => u.roles.some(r => r.toLowerCase().includes(q)) ||
            ['email', 'first_name', 'last_name', 'full_name'].some(k => u[k]?.toLowerCase()?.includes(q))
        );
        this.table.offset = 0;
        this.table.limit=100
    }

    filterUpdate(event) {
        // Reset ng-select on search
        this.selectedRole = this.selectRole[0];
        this.selectedPlan = this.selectPlan[0];
        this.selectedStatus = this.selectStatus[0];

        const val = event.target.value.toLowerCase();

        // Filter Our Data
        const temp = this.tempData.filter(function (d) {
            return d.fullName.toLowerCase().indexOf(val) !== -1 || !val;
        });

        // Update The Rows
        this.users = temp;
        // Whenever The Filter Changes, Always Go Back To The First Page
        this.table.offset = 0;
    }


    filterByRole() {
        if (!this.selectedRole) {
            this.users = this.originalUsers;
            return;
        }
        const role = this.selectedRole;
        console.log('mr role : ', role);
        this.users = this.originalUsers.filter(u => u.roles.includes(role));
    }

    filterByPlan(event) {
        const filter = event ? event.value : '';
        this.previousPlanFilter = filter;
        this.temp = this.filterRows(this.previousRoleFilter, filter, this.previousStatusFilter);
        this.users = this.temp;
    }

    edit(user) {
        console.log('row: ', user);
        const modal = this.modal.open(UserEditComponent, {
            size: 'lg'
        });
        modal.componentInstance.oldUser = user;
    }

    openLimits(u: User1) {
        let modal = this.modal.open(UserLimitsComponent, {
            centered: true,
            size: 'lg'
        });
        modal.componentInstance.userId = u.id
    }
    add() {
        this.modal.open(UserAddComponent, {
            size: 'lg'
        });
    }


    filterByStatus(event) {
        const filter = event ? event.value : '';
        this.previousStatusFilter = filter;
        this.temp = this.filterRows(this.previousRoleFilter, this.previousPlanFilter, filter);
        this.users = this.temp;
    }

    filterRows(roleFilter, planFilter, statusFilter): any[] {
        // Reset search on select change
        this.searchValue = '';

        roleFilter = roleFilter.toLowerCase();
        planFilter = planFilter.toLowerCase();
        statusFilter = statusFilter.toLowerCase();

        return this.tempData.filter(row => {
            const isPartialNameMatch = row.role.toLowerCase().indexOf(roleFilter) !== -1 || !roleFilter;
            const isPartialGenderMatch = row.currentPlan.toLowerCase().indexOf(planFilter) !== -1 || !planFilter;
            const isPartialStatusMatch = row.status.toLowerCase().indexOf(statusFilter) !== -1 || !statusFilter;
            return isPartialNameMatch && isPartialGenderMatch && isPartialStatusMatch;
        });
    }


}
