import {Component, EventEmitter, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-org-add',
  templateUrl: './org-add.component.html',
  styleUrls: ['./org-add.component.scss']
})
export class OrgAddComponent {
  constructor(public activeModal: NgbActiveModal, private _toastrService: ToastrService) {
  }
  org = {
    name: '',
    img: null
  }
  @Output() created = new EventEmitter()
  loading = false
  submitted = false
  save() {
    this.submitted = true
    console.log('org name : ', this.org.name)
    if (!this.org.name || (this.org.name?.length < 3)) { return }
    this.loading = true
    setTimeout(() => {
      this._toastrService.success('Organization Saved Successfully')
      this.created.emit(this.org)
      this.loading = false
      this.activeModal.dismiss()
    }, 1000)
    console.log('org : ', this.org)
  }
  upload(e) {
    console.log('event : ', e.target?.files)
    let file=e.target?.files?.[0]
    if(!file) return
    const reader = new FileReader();
    reader.onload = (e: any) => {
      this.org.img = e.target.result;
    };
    reader.readAsDataURL(file);
  }

}
