import {CommonModule} from '@angular/common';
import {NgModule} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';

import {NgbActiveModal, NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {NgSelectModule} from '@ng-select/ng-select';
import {NgxDatatableModule} from '@swimlane/ngx-datatable';
import {Ng2FlatpickrModule} from 'ng2-flatpickr';

import {CoreCommonModule} from '@core/common.module';
import {CoreDirectivesModule} from '@core/directives/directives';
import {CorePipesModule} from '@core/pipes/pipes.module';
import {CoreSidebarModule} from '@core/components';


import {UserEditComponent} from 'app/main/apps/user/user-edit/user-edit.component';
import {UserEditService} from 'app/main/apps/user/user-edit/user-edit.service';

import {UserListComponent} from 'app/main/apps/user/user-list/user-list.component';
import {UserListService} from 'app/main/apps/user/user-list/user-list.service';

import {UserViewComponent} from 'app/main/apps/user/user-view/user-view.component';
import {UserViewService} from 'app/main/apps/user/user-view/user-view.service';
import {NewUserSidebarComponent} from 'app/main/apps/user/user-list/new-user-sidebar/new-user-sidebar.component';
import {UserHeaderComponent} from './user-header/user-header.component';
import {OrgsComponent} from './orgs/orgs.component';
import {OrgAddComponent} from './org-add/org-add.component';
import {OrgEditComponent} from './org-edit/org-edit.component';
import {OrgShowComponent} from './org-show/org-show.component';
import {ComponentsModule} from '../../components/components.module';
import { UserAddComponent } from './user-add/user-add.component';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { UserLimitsComponent } from './user-limits/user-limits.component';

// routing
const routes: Routes = [
    {
        path: 'user-list',
        component: UserListComponent,
        resolve: {
            uls: UserListService
        },
        data: {animation: 'UserListComponent'}
    },
    {
        path: 'user-view/:id',
        component: UserViewComponent,
        resolve: {
            data: UserViewService,
        },
        data: {path: 'view/:id', animation: 'UserViewComponent'}
    },
    {
        path: 'user-edit/:id',
        component: UserEditComponent,
        resolve: {
            ues: UserEditService
        },
        data: {animation: 'UserEditComponent'}
    },
    {
        path: 'user-view',
        redirectTo: '/apps/user/user-view/2' // Redirection
    },
    {
        path: 'user-edit',
        redirectTo: '/apps/user/user-edit/2' // Redirection
    },
    {
        path: 'orgs/:orgId',
        component: OrgShowComponent,
        resolve: {
            uls: UserListService
        },
        data: {animation: 'UserListComponent'}
    },

    {
        path: 'orgs',
        component: OrgsComponent,
        resolve: {
            uls: UserListService
        },
        data: {animation: 'UserListComponent'}
    },
];

@NgModule({
    declarations: [UserListComponent, UserViewComponent,
        UserEditComponent, NewUserSidebarComponent, UserHeaderComponent,
        OrgsComponent, OrgAddComponent, OrgEditComponent, OrgShowComponent, UserAddComponent, UserProfileComponent, UserLimitsComponent],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        CoreCommonModule,
        FormsModule,
        ReactiveFormsModule,
        NgbModule,
        NgSelectModule,
        Ng2FlatpickrModule,
        NgxDatatableModule,
        CorePipesModule,
        CoreDirectivesModule,
        CoreSidebarModule,
        ComponentsModule
    ],
    providers: [UserListService, UserViewService, UserEditService, NgbActiveModal]
})
export class UserModule {
}
